import {Directive, Input} from '@angular/core';
import {VdlDialogRef} from './dialog-ref';


/**
 * Button that will close the current dialog.
 */
@Directive({
  selector: 'button[vdl-dialog-close]',
  host: {
    '(click)': 'dialogRef.close()',
    '[attr.aria-label]': 'ariaLabel',
    'type': 'button', // Prevents accidental form submits.
  }
})
export class VdlDialogClose {
  /** Screenreader label for the button. */
  @Input('aria-label') ariaLabel: string = 'Close dialog';

  constructor(public dialogRef: VdlDialogRef<any>) { }
}

/**
 * Title of a dialog element. Stays fixed to the top of the dialog when scrolling.
 */
@Directive({
  selector: '[vdl-dialog-title]',
  host: {
    '[class.vdl-dialog-title]': 'true'
  }
})
export class VdlDialogTitle { }


/**
 * Scrollable content container of a dialog.
 */
@Directive({
  selector: '[vdl-dialog-content], vdl-dialog-content',
  host: {
    '[class.vdl-dialog-content]': 'true'
  }
})
export class VdlDialogContent { }


/**
 * Container for the bottom action buttons in a dialog.
 * Stays fixed to the bottom when scrolling.
 */
@Directive({
  selector: '[vdl-dialog-actions], vdl-dialog-actions',
  host: {
    '[class.vdl-dialog-actions]': 'true'
  }
})
export class VdlDialogActions { }
